import { createAsyncThunk } from '@reduxjs/toolkit'
import { AxiosError } from 'axios'
import {
  API_VACANCIES_ENDPOINT,
  API_CURRENCY_DIRECTORY_ENDPOINT,
  API_VACANCY_BY_ID_ENDPOINT,
  API_SOURCE_DIRECTORY_ENDPOINT,
} from '../../utils/constants'
import showAchievementsNotifications from '../../utils/showAchievementsNotifications'
import api from './settings'

export const getResponses = createAsyncThunk('diary/getResponses', async (searchQuery: string, { rejectWithValue }) => {
  try {
    const response = await api.get(`${API_VACANCIES_ENDPOINT}${searchQuery ? `?search=${searchQuery}` : ''}`)
    return response.data
  } catch (error) {
    const typedError = error as AxiosError
    return rejectWithValue(typedError?.response?.data)
  }
})

export const getCurrencies = createAsyncThunk('diary/getCurrencies', async (_: void, { rejectWithValue }) => {
  try {
    const response = await api.get(API_CURRENCY_DIRECTORY_ENDPOINT)
    return response.data
  } catch (error) {
    const typedError = error as AxiosError
    return rejectWithValue(typedError?.response?.data)
  }
})

export const getSources = createAsyncThunk('diary/getSources', async (_: void, { rejectWithValue }) => {
  try {
    const response = await api.get(API_SOURCE_DIRECTORY_ENDPOINT)
    return response.data
  } catch (error) {
    const typedError = error as AxiosError
    return rejectWithValue(typedError?.response?.data)
  }
})

export const createResponse = createAsyncThunk(
  'diary/createResponse',
  async (data: FormData, { dispatch, rejectWithValue }) => {
    try {
      const response = await api.post(API_VACANCIES_ENDPOINT, data)
      showAchievementsNotifications(response.data?.achievements, dispatch)
      return response.data
    } catch (error) {
      const typedError = error as AxiosError
      return rejectWithValue(typedError?.response?.data)
    }
  },
)

export const patchResponse = createAsyncThunk(
  'diary/patchResponse',
  async (rawData: { id: number; data: { [value: string]: string } }, { dispatch, rejectWithValue }) => {
    try {
      const { id, data } = rawData
      const response = await api.patch(API_VACANCY_BY_ID_ENDPOINT(id), data)
      showAchievementsNotifications(response.data?.achievements, dispatch)
      return response.data
    } catch (error) {
      const typedError = error as AxiosError
      return rejectWithValue(typedError?.response?.data)
    }
  },
)

export const moveCards = createAsyncThunk(
  'diary/moveCards',
  async (
    rawData: { id: number; position: number; statusData?: { [value: string]: string; last_update_datetime: string } },
    { dispatch, rejectWithValue },
  ) => {
    try {
      const { id, position, statusData } = rawData
      let requestData = { card_position: position }
      if (statusData) {
        requestData = { ...requestData, ...statusData }
      }
      const response = await api.patch(API_VACANCY_BY_ID_ENDPOINT(id), requestData)
      showAchievementsNotifications(response.data?.achievements, dispatch)
      return response.data
    } catch (error) {
      const typedError = error as AxiosError
      return rejectWithValue(typedError?.response?.data)
    }
  },
)

export const deleteResponse = createAsyncThunk('diary/deleteResponse', async (id: string, { rejectWithValue }) => {
  try {
    await api.delete(API_VACANCY_BY_ID_ENDPOINT(id))
    return id
  } catch (error) {
    const typedError = error as AxiosError
    return rejectWithValue(typedError?.response?.data)
  }
})
