import { createSlice } from '@reduxjs/toolkit'
import { IAPIError } from '../types/global-types'
import { IInitialState } from '../types/workshop/workshop-types'
import { createResponse, getProjects } from '../axios/student-projects'

export const initialState: IInitialState = {
  isLockUpdate: false,
  filters: {},
  isLoading: false,
  isNetworkError: false,
  networkError: null,
  studentsProjects: [],
  charityProjects: [],
  projectToView: null,
  isSuccessResponse: false,
}

const studentProjectsSlice = createSlice({
  name: 'studentsWorkshop',
  initialState,
  reducers: {
    resetSuccessResponseStatus(state) {
      state.isSuccessResponse = false
    },
    saveFilters(state, action) {
      state.filters = action.payload
    },
    setIsLockUpdate(state, action) {
      state.isLockUpdate = action.payload
    },
    setProjectToView(state, action) {
      state.projectToView = action.payload
    },
    setCharityProjectToViewed(state, action) {
      const newProjects = [...state.charityProjects]
      const changedIndex = state.charityProjects.findIndex((item) => item.id === action.payload)
      newProjects[changedIndex].is_viewed = true
      state.charityProjects = [...newProjects]
    },
    setStudentProjectToViewed(state, action) {
      const newProjects = [...state.studentsProjects]
      const changedIndex = state.studentsProjects.findIndex((item) => item.id === action.payload)
      newProjects[changedIndex].is_viewed = true
      state.studentsProjects = [...newProjects]
    },
  },
  extraReducers: (builder) => {
    builder
      // Get projects
      .addCase(getProjects.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getProjects.fulfilled, (state, action) => {
        state.isLoading = false
        state.studentsProjects = action.payload.studentsProjects
        state.charityProjects = action.payload.charityProjects
      })
      .addCase(getProjects.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
      //  create response
      .addCase(createResponse.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(createResponse.fulfilled, (state, action) => {
        state.isLoading = false
        const isCharity = state.charityProjects.findIndex((item) => item.id === action.payload) >= 0
        if (isCharity) {
          const newProjects = [...state.charityProjects]
          const changedIndex = state.charityProjects.findIndex((item) => item.id === action.payload)
          newProjects[changedIndex].is_accepted = true
          state.charityProjects = [...newProjects]
          if (state.projectToView) state.projectToView.is_accepted = true
        } else {
          const newProjects = [...state.studentsProjects]
          const changedIndex = state.studentsProjects.findIndex((item) => item.id === action.payload)
          newProjects[changedIndex].is_accepted = true
          state.studentsProjects = [...newProjects]
          if (state.projectToView) state.projectToView.is_accepted = true
        }
        state.isSuccessResponse = true
      })
      .addCase(createResponse.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
  },
})

export const {
  setIsLockUpdate,
  saveFilters,
  setProjectToView,
  setCharityProjectToViewed,
  setStudentProjectToViewed,
  resetSuccessResponseStatus,
} = studentProjectsSlice.actions

export default studentProjectsSlice
