import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import {
  TInitialPCState,
  TTeamItem,
} from '../types/workshop-projects-list-page-types/workshop-projects-list-page-types'
import { IAPIError } from '../types/global-types'
import { getDirectories } from '../axios/project-creation'

const initialState: TInitialPCState = {
  currentTab: 0,
  isLockNextButton: false,
  isLoading: false,
  isNetworkError: false,
  networkError: null,
  project_types: [],
  selectedProjectType: null,
  teamItems: [],
  tabPC2Values: null,
  tabPC3Values: null,
  tabPC4Values: null,
  project_directions: [],
  project_spheres: [],
  customer_types: [],
  selectedCustomerType: null,
  selectedFaculties: [],
  customer_comment: '',
  createStatus: '',
}

export const projectCreationSlice = createSlice({
  name: 'project-creation-slice',
  initialState,
  reducers: {
    resetState: (state) => initialState,
    resetCreateStatus: (state) => {
      state.createStatus = ''
    },
    setCustomerType: (state, action) => {
      state.selectedCustomerType = action.payload
    },
    // TODO      .     
    setProjectsValues(state, action) {
      const { fieldName, values } = action.payload
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      state[fieldName] = values
    },
    setIsLockNextButton(state, action) {
      state.isLockNextButton = action.payload
    },
    setProjectType: (state, action) => {
      state.selectedProjectType = action.payload
    },
    setNextTab: (state) => {
      state.currentTab += 1
    },
    setPrevTab: (state) => {
      if (state.currentTab) state.currentTab -= 1
    },
    changeActiveStatus: (state, action) => {
      const newTeamItems = [...state.teamItems]
      const itemIndex = newTeamItems.findIndex((item) => item.id === action.payload)
      //    ,   
      if (newTeamItems[itemIndex].isActive) {
        newTeamItems[itemIndex] = { ...newTeamItems[itemIndex], isActive: !newTeamItems[itemIndex].isActive, count: 0 }
      } else {
        newTeamItems[itemIndex] = { ...newTeamItems[itemIndex], isActive: !newTeamItems[itemIndex].isActive, count: 1 }
      }
      state.teamItems = [...newTeamItems]
    },
    increaseTeamItemCounter: (state, action) => {
      const newTeamItems = [...state.selectedFaculties]
      const itemIndex = newTeamItems.findIndex((item) => item.id === action.payload)
      newTeamItems[itemIndex] = { ...newTeamItems[itemIndex], count: newTeamItems[itemIndex].count + 1 }
      state.selectedFaculties = [...newTeamItems]
    },
    decreaseTeamItemCounter: (state, action) => {
      const newTeamItems = [...state.selectedFaculties]
      const itemIndex = newTeamItems.findIndex((item) => item.id === action.payload)
      const foundedItem = newTeamItems[itemIndex]
      if (foundedItem.count !== 1) {
        newTeamItems[itemIndex] = { ...foundedItem, count: newTeamItems[itemIndex].count - 1 }
      }
      state.selectedFaculties = [...newTeamItems]
    },
  },
  extraReducers: (builder) => {
    builder
      // get directories
      .addCase(getDirectories.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getDirectories.fulfilled, (state, action) => {
        state.isLoading = false
        const { project_types, team_items, project_directions, project_spheres, customer_types } = action.payload
        state.project_types = project_types
        state.teamItems = team_items
        state.project_directions = project_directions
        state.project_spheres = project_spheres
        state.customer_types = customer_types
      })
      .addCase(getDirectories.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
  },
})

export const {
  setNextTab,
  setPrevTab,
  changeActiveStatus,
  increaseTeamItemCounter,
  decreaseTeamItemCounter,
  setProjectType,
  setIsLockNextButton,
  setProjectsValues,
  setCustomerType,
  resetCreateStatus,
  resetState,
} = projectCreationSlice.actions
export default projectCreationSlice
