import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import {
  ICompany,
  IDirectories,
  IFilterJson,
  IInfoPopupAction,
  IInitialState,
} from '../types/companies-page-types/companies-page-types'
import {
  deleteCompany,
  getCohorts,
  getCompaniesList,
  getCompaniesStatuses,
  getCompanyEmployedStudentList,
  getCompanyInfo,
  getCompanyVacanciesList,
  getDirectories,
  patchCompanyInfo,
  patchCompanyStatus,
  patchStudentStatus,
  patchVacancyStatus,
  postDraftCompany,
} from '../axios/companies-page'
import { IAPIError } from '../types/global-types'

export const initialState: IInitialState = {
  filtersJSON: {} as IFilterJson,
  directoriesData: {} as IDirectories,
  companies: [],
  selectedCompanyVacancies: [],
  selectedCompany: null,
  isLoading: false,
  isNetworkError: false,
  networkError: null,
  updateStatus: '',
  updateCompanyStatus: false,
  rowsCount: 0,
  companiesStatuses: [],
  selectedCompanyFullInfo: null,
  employedStudentList: [],
  coursesWithCohorts: [],
  isOpenNewEditCompanyModal: false,
  isLockUpdate: false,
  companiesRowsCount: 0,
  filters: {},
  infoPopupTitle: '',
  infoPopupSubtitle: '',
  infoPopupButtonTitle: '',
  isInfoPopupOpen: false,
  infoPopupType: '',
  deleteNetworkError: null,
  vacancyToArchive: null,
  studentToEdit: null,
  isOpenFoundJobPopup: false,
  isOpenAcceleratePopup: false,
  isDraftPopupOpen: false,
  draftFormData: null,
}

const companiesPageSlice = createSlice({
  name: 'companies-page',
  initialState,
  reducers: {
    setDraftPopupState(state, action) {
      state.isDraftPopupOpen = action.payload
    },
    setDraftFormData(state, action) {
      state.draftFormData = action.payload
    },
    setIsOpenAcceleratePopup(state, action) {
      state.isOpenAcceleratePopup = action.payload
    },
    updateStudentStatus(state, action) {
      const newStudentList = [...state.employedStudentList]
      newStudentList[newStudentList.findIndex((el) => el.id === state.studentToEdit?.id)].status = action.payload
      state.employedStudentList = newStudentList
    },
    setIsOpenFoundJobPopup(state, action) {
      state.isOpenFoundJobPopup = action.payload
    },
    setStudentToEdit(state, action) {
      state.studentToEdit = action.payload
    },
    setVacancyToArchive(state, action) {
      state.vacancyToArchive = action.payload
    },
    clearDeleteError(state) {
      state.deleteNetworkError = null
      state.isNetworkError = false
    },
    openInfoPopup(state, action: PayloadAction<IInfoPopupAction>) {
      state.infoPopupTitle = action.payload.title
      state.infoPopupSubtitle = action.payload.subtitle
      state.infoPopupButtonTitle = action.payload.buttonTitle
      state.infoPopupType = action.payload.type
      state.isInfoPopupOpen = true
    },
    closeInfoPopup(state) {
      state.isInfoPopupOpen = false
      state.infoPopupTitle = ''
      state.infoPopupSubtitle = ''
      state.infoPopupType = ''
    },
    openNewEditCompanyModal(state) {
      state.isOpenNewEditCompanyModal = true
    },
    closeNewEditCompanyModal(state) {
      state.isOpenNewEditCompanyModal = false
    },
    setSaveFilters(state, action) {
      state.filters = action.payload
    },
    setLockUpdate(state, action) {
      state.isLockUpdate = action.payload
    },
    setFiltersJSON(state, action) {
      state.filtersJSON = action.payload
    },
    setDirectoriesData(state, action) {
      state.directoriesData = action.payload
    },
    setSelectedCompany(state, action) {
      state.selectedCompany = action.payload
    },
    updateCompanyInList(state, action) {
      const newCompanies = [...state.companies]
      const companyIndex = newCompanies?.findIndex((company) => company.pk === action.payload.id)
      newCompanies[companyIndex] = { ...action.payload, pk: action.payload.id }
      state.companies = [...newCompanies]
    },
    setSelectedCompanyFullInfo(state, action) {
      state.selectedCompanyFullInfo = action.payload
    },
    resetSelectedCompanyFullInfo(state) {
      state.selectedCompanyFullInfo = null
    },
    setVacancies(state, action) {
      state.companies = action.payload
    },
    setIsLoading(state, action) {
      state.isLoading = action.payload
    },
    resetUpdateStatus(state) {
      state.updateStatus = ''
    },
    resetUpdateStudentStatus(state) {
      state.updateCompanyStatus = false
    },
    resetDataGridInfo(state) {
      state.employedStudentList = []
      state.selectedCompanyVacancies = []
    },
  },
  extraReducers: (builder) => {
    builder
      // get Companies
      .addCase(getCompaniesList.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getCompaniesList.fulfilled, (state, action) => {
        state.isLoading = false
        const list: Array<ICompany> = action.payload.results
        state.companies = list.map((item) => ({ ...item, id: item.pk }))
        // if (action.payload.links.previous === null) {
        //   state.companies = list.map((item) => ({ ...item, id: item.pk }))
        // } else {
        //   state.companies = state.companies.concat(...list.map((item) => ({ ...item, id: item.pk })))
        // }
        state.companiesRowsCount = action.payload.count
      })
      .addCase(getCompaniesList.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get company vacancies
      .addCase(getCompanyVacanciesList.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getCompanyVacanciesList.fulfilled, (state, action) => {
        state.isLoading = false
        state.selectedCompanyVacancies = action.payload.results
        state.rowsCount = action.payload.count
      })
      .addCase(getCompanyVacanciesList.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get company employed students
      .addCase(getCompanyEmployedStudentList.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getCompanyEmployedStudentList.fulfilled, (state, action) => {
        state.isLoading = false
        state.employedStudentList = action.payload.results
        state.rowsCount = action.payload.count
      })
      .addCase(getCompanyEmployedStudentList.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get company full information
      .addCase(getCompanyInfo.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getCompanyInfo.fulfilled, (state, action) => {
        state.isLoading = false
        state.selectedCompanyFullInfo = action.payload
        state.rowsCount = action.payload.count
      })
      .addCase(getCompanyInfo.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // delete company
      .addCase(deleteCompany.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(deleteCompany.fulfilled, (state, action) => {
        state.isLoading = false
        const newCompanies = [...state.companies]
        newCompanies.splice(
          newCompanies.findIndex((item) => item.id === state.selectedCompany?.id),
          1,
        )
        state.companies = [...newCompanies]
        state.selectedCompany = null
      })
      .addCase(deleteCompany.rejected, (state, action) => {
        state.isLoading = false
        state.deleteNetworkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get companies statuses
      .addCase(getCompaniesStatuses.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getCompaniesStatuses.fulfilled, (state, action) => {
        state.isLoading = false
        state.companiesStatuses = action.payload
        state.filtersJSON = {
          ...state.filtersJSON,
          statuses: action.payload,
        }
        state.rowsCount = action.payload.count
      })

      // get company full information
      .addCase(getCohorts.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getCohorts.fulfilled, (state, action) => {
        state.isLoading = false
        state.coursesWithCohorts = action.payload.courses
      })
      .addCase(getCohorts.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      .addCase(getCompaniesStatuses.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get Directories
      .addCase(getDirectories.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getDirectories.fulfilled, (state, action) => {
        state.isLoading = false
        state.filtersJSON = { courses: action.payload.filters.courses, statuses: state.filtersJSON.statuses }
        state.directoriesData = action.payload.directoriesData
      })
      .addCase(getDirectories.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // // patch student status
      .addCase(patchStudentStatus.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchStudentStatus.fulfilled, (state, action) => {
        state.isLoading = false
        const { updatedRow } = action.payload
        const newStudentList = [...state.employedStudentList]
        newStudentList[newStudentList.findIndex((el) => el.id === updatedRow.id)].status = updatedRow.status
        state.employedStudentList = newStudentList
        state.studentToEdit = null
        // state.updateStatus = 'ok'
      })
      .addCase(patchStudentStatus.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // patch company status
      .addCase(patchCompanyStatus.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchCompanyStatus.fulfilled, (state, action) => {
        state.isLoading = false
        const { updatedRow } = action.payload
        const newCompanies = [...state.companies]
        newCompanies[newCompanies.findIndex((el) => el.id === updatedRow.id)] = updatedRow
        state.companies = newCompanies
        state.updateStatus = 'ok'
      })
      .addCase(patchCompanyStatus.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // patch company info
      .addCase(patchCompanyInfo.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchCompanyInfo.fulfilled, (state, action) => {
        state.isLoading = false
        const updatedRow = action.payload
        state.selectedCompanyFullInfo = { ...updatedRow }
        const newCompanies = [...state.companies]
        newCompanies[newCompanies.findIndex((el) => el.pk === updatedRow.id)] = {
          ...newCompanies[newCompanies.findIndex((el) => el.pk === updatedRow.id)],
          courses: [...updatedRow.courses],
          name: updatedRow.name,
        }
        state.companies = newCompanies
      })

      // patch Vacancies
      .addCase(patchVacancyStatus.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchVacancyStatus.fulfilled, (state, action) => {
        state.isLoading = false
        const { updatedRow } = action.payload
        const newVacancies = [...state.selectedCompanyVacancies]
        newVacancies[newVacancies.findIndex((el) => el.id === updatedRow.id)].vacancy_status = updatedRow.vacancy_status
        state.selectedCompanyVacancies = newVacancies
      })
      .addCase(patchVacancyStatus.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
      // post Draft Company
      .addCase(postDraftCompany.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(postDraftCompany.fulfilled, (state, action) => {
        state.isLoading = false
      })
      .addCase(postDraftCompany.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
  },
})

export const {
  setFiltersJSON,
  setDirectoriesData,
  setVacancies,
  setIsLoading,
  resetUpdateStatus,
  resetUpdateStudentStatus,
  setSelectedCompany,
  setSelectedCompanyFullInfo,
  resetSelectedCompanyFullInfo,
  resetDataGridInfo,
  openNewEditCompanyModal,
  closeNewEditCompanyModal,
  setLockUpdate,
  setSaveFilters,
  openInfoPopup,
  closeInfoPopup,
  clearDeleteError,
  setVacancyToArchive,
  setStudentToEdit,
  setIsOpenFoundJobPopup,
  updateStudentStatus,
  setIsOpenAcceleratePopup,
  updateCompanyInList,
  setDraftPopupState,
  setDraftFormData,
} = companiesPageSlice.actions

export default companiesPageSlice
