import { createSlice } from '@reduxjs/toolkit'
import { createPartnerForm, getDirectories } from '../axios/partners-form'
import { IAPIError } from '../types/global-types'

export interface IInitialState {
  managers: Array<{ id: string; name: string }>
  types: Array<{ id: string; name: string }>
  scopes: Array<{ id: string; name: string }>
  sizes: Array<{ id: string; name: string }>
  workFormats: Array<{ id: string; name: string }>
  employments: Array<{ id: string; name: string }>
  isLoading: boolean
  isNetworkError: boolean
  networkError: IAPIError | null
  formData: { [key: string]: any }
  currentTab: number
}

export const initialState: IInitialState = {
  managers: [],
  types: [],
  scopes: [],
  sizes: [],
  workFormats: [],
  employments: [],
  isLoading: false,
  isNetworkError: false,
  networkError: null,
  formData: {},
  currentTab: 0,
}

const partnersFormSlice = createSlice({
  name: 'partners-form',
  initialState,
  reducers: {
    setFormData: (state, action) => {
      state.formData = action.payload
    },
    setCurrentTab: (state, action) => {
      state.currentTab = action.payload
    },
    resetPartnersForm: () => initialState,
  },
  extraReducers: (builder) => {
    builder
      // get Directories
      .addCase(getDirectories.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getDirectories.fulfilled, (state, action) => {
        state.isLoading = false
        state.managers = action.payload.managers
        state.types = action.payload.types
        state.scopes = action.payload.scopes
        state.sizes = action.payload.sizes
        state.workFormats = action.payload.workFormats
        state.employments = action.payload.employments
      })
      .addCase(getDirectories.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // CREATE PARTNERS FORM
      .addCase(createPartnerForm.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(createPartnerForm.fulfilled, (state, action) => {
        state.isLoading = false
      })
      .addCase(createPartnerForm.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
  },
})

export const { setFormData, setCurrentTab, resetPartnersForm } = partnersFormSlice.actions

export default partnersFormSlice
