import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { IAPIError } from '../types/global-types'
import {
  deleteAllShowedNotifications,
  getNotShowedNotificationsList,
  getShowedNotificationsList,
  patchAllNotifications,
  patchNotification,
} from '../axios/hr-notifications'
import { TInitialState } from '../types/hr-notifications/hr-notifications'

const initialState: TInitialState = {
  showedNotifications: [],
  notShowedNotifications: [],
  notificationsCount: 0,
  isNetworkError: false,
  networkError: null,
  isLoading: false,
  externalPage: undefined,
}

export const hrNotificationsSlice = createSlice({
  name: 'hr-notifications',
  initialState,
  reducers: {
    changeNotificationsCountState: (state, action: PayloadAction<number>) => {
      state.notificationsCount = action.payload
    },
    resetPages: (state) => {
      state.externalPage = undefined
    },
  },
  extraReducers: (builder) => {
    builder
      // get showed notifications
      .addCase(getShowedNotificationsList.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getShowedNotificationsList.fulfilled, (state, action) => {
        state.isLoading = false
        const searchParams = new URLSearchParams(action.payload?.next)
        const page = searchParams.get('page')
        if (state.externalPage) {
          state.showedNotifications.push(...action.payload?.results)
        } else {
          state.showedNotifications = action.payload?.results
        }
        state.externalPage = page
        // state.isExternalLoading = false
      })
      .addCase(getShowedNotificationsList.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
      // // get not showed notifications
      .addCase(getNotShowedNotificationsList.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getNotShowedNotificationsList.fulfilled, (state, action) => {
        state.isLoading = false
        // state.notShowedNotifications = action.payload.results
        const searchParams = new URLSearchParams(action.payload?.next)
        const page = searchParams.get('page')
        if (state.externalPage) {
          state.notShowedNotifications.push(...action.payload?.results)
        } else {
          state.notShowedNotifications = action.payload?.results
        }
        state.externalPage = page
        // state.isExternalLoading = false
      })
      .addCase(getNotShowedNotificationsList.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
      // patch not showed notifications
      .addCase(patchNotification.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchNotification.fulfilled, (state, action) => {
        state.isLoading = false
        state.showedNotifications.push(action.payload)
        const newNotShowedNotifications = [...state.notShowedNotifications]
        newNotShowedNotifications.splice(
          newNotShowedNotifications.findIndex((item) => item.pk === action.payload.pk),
          1,
        )
        state.notShowedNotifications = [...newNotShowedNotifications]
      })
      .addCase(patchNotification.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
      // patch all not showed notifications
      .addCase(patchAllNotifications.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchAllNotifications.fulfilled, (state, action) => {
        state.isLoading = false
        state.showedNotifications.push(...state.notShowedNotifications)
        state.notShowedNotifications = []
      })
      .addCase(patchAllNotifications.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
      // delete all showed notifications
      .addCase(deleteAllShowedNotifications.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(deleteAllShowedNotifications.fulfilled, (state, action) => {
        state.isLoading = false
        state.showedNotifications = []
      })
      .addCase(deleteAllShowedNotifications.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
  },
})

export const { changeNotificationsCountState, resetPages } = hrNotificationsSlice.actions
export default hrNotificationsSlice
