import { createAsyncThunk } from '@reduxjs/toolkit'
import { AxiosError } from 'axios'
import api from './settings'
import PartnersFormConstants from '../../utils/constants/partners-form-constants'

export const getDirectories = createAsyncThunk('partners-form/getDirectories', async (_: void, { rejectWithValue }) => {
  try {
    const responseManager = await api.get(PartnersFormConstants.API_MANAGERS)
    const responseScope = await api.get(PartnersFormConstants.API_SCOPE)
    const responseType = await api.get(PartnersFormConstants.API_TYPE)
    const responseSize = await api.get(PartnersFormConstants.API_SIZE)
    const responseWorkFormat = await api.get(PartnersFormConstants.API_WORKFORMAT)
    const responseEmployment = await api.get(PartnersFormConstants.API_EMPLOYMENT)
    const managersData = responseManager?.data?.map((m: { id: number; first_name: string; last_name: string }) => ({
      id: m.id,
      name: `${m.first_name} ${m.last_name}`,
    }))
    const typesData = responseType?.data?.map((t: { id: number; value: string }) => ({ id: t.id, name: t.value }))
    const scopeData = responseScope?.data?.map((s: { id: number; value: string }) => ({ id: s.id, name: s.value }))
    const sizeData = responseSize?.data?.map((s: { id: number; value: string }) => ({ id: s.id, name: s.value }))
    const workFormatData = responseWorkFormat?.data?.map((s: { id: number; value: string }) => ({
      id: s.id,
      name: s.value,
    }))
    const employmentData = responseEmployment?.data?.map((s: { id: number; value: string }) => ({
      id: s.id,
      name: s.value,
    }))
    return {
      managers: managersData,
      types: typesData,
      scopes: scopeData,
      sizes: sizeData,
      workFormats: workFormatData,
      employments: employmentData,
    }
  } catch (e) {
    const typedError = e as AxiosError
    return rejectWithValue(typedError?.response)
  }
})

export const createPartnerForm = createAsyncThunk(
  'partners-form/createPartnerForm',
  async (data: FormData, { rejectWithValue }) => {
    try {
      const response = await api.post(PartnersFormConstants.API_CREATE, data, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      })
      return response.data
    } catch (error) {
      const typedError = error as AxiosError
      return rejectWithValue(typedError?.response?.data)
    }
  },
)
