import { createSlice } from '@reduxjs/toolkit'
import { SnackbarKey, OptionsObject } from 'notistack'
import internal from 'stream'
import {
  createResponse,
  deleteResponse,
  getCurrencies,
  getResponses,
  getSources,
  moveCards,
  patchResponse,
} from '../axios/diary'
import { IAPIError, IRequestInfo } from '../types/global-types'

type Response = {
  id: string
  key: SnackbarKey
  dismissed: boolean
  message: string
  options?: OptionsObject
}

type Currency = {
  id: number
  name: string
  icon: string
}

type VacancySource = {
  id: number
  name: string
}

type ApiCurrency = {
  id: number
  name: string
  label: string
}

type DragStatus = {
  stage: 'idle' | 'dragging' | 'dropped'
  responseId: string | null
  oldStatus: string | null
  newStatus: string | null
  responseDate: string | null
}

type CardsToRefreshPosition = {
  id: number
  position: number
  statusData: Record<string, unknown>
}

interface IDiaryState extends IRequestInfo {
  responses: Array<Response>
  currencies: Array<Currency>
  sources: Array<VacancySource>
  draggingStatus: DragStatus
  cardsToRefreshPositions: Array<CardsToRefreshPosition>
  searchQuery?: string
}

export const initialState: IDiaryState = {
  responses: [],
  currencies: [],
  sources: [],
  status: 'idle',
  isError: false,
  errorInfo: null,
  cardsToRefreshPositions: [],
  draggingStatus: {
    stage: 'idle',
    responseId: null,
    oldStatus: null,
    newStatus: null,
    responseDate: null,
  },
  searchQuery: undefined,
}

const diarySlice = createSlice({
  name: 'diary',
  initialState,
  reducers: {
    setSearchQuery(state, action) {
      if (action.payload === '') {
        state.searchQuery = undefined
      } else {
        state.searchQuery = action.payload
      }
    },
    addToQueue(state, action) {
      const { id, position, statusData } = action.payload
      state.cardsToRefreshPositions.push({ id, position, statusData })
    },
    removeFromQueue(state, action) {
      state.cardsToRefreshPositions = state.cardsToRefreshPositions.filter((card) => card.id !== action.payload)
    },
    setResponses(state, action) {
      state.responses = action.payload
    },
    updateResponseStatus(state, action) {
      state.draggingStatus = {
        stage: 'dragging',
        responseId: action.payload.id,
        oldStatus: action.payload.status_id,
        newStatus: action.payload.newStatus,
        responseDate: action.payload.response_date,
      }
      state.responses = state.responses.map((response) =>
        response.id === action.payload.id ? { ...response, status_id: action.payload.newStatus } : response,
      )
    },
    cancelDragging(state) {
      state.responses = [...state.responses].map((response) =>
        response.id === state.draggingStatus?.responseId
          ? { ...response, status_id: state.draggingStatus?.oldStatus }
          : response,
      )
      state.draggingStatus = {
        stage: 'idle',
        responseId: null,
        oldStatus: null,
        newStatus: null,
        responseDate: null,
      }
    },
    finishDragging(state) {
      state.draggingStatus = {
        stage: 'dropped',
        responseId: null,
        oldStatus: null,
        newStatus: null,
        responseDate: null,
      }
    },
  },
  extraReducers: (builder) => {
    builder
      // GET CARDS
      .addCase(getResponses.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(getResponses.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.responses = action.payload
      })
      .addCase(getResponses.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })

      // GET CURRENCIES
      .addCase(getCurrencies.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(getCurrencies.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.currencies = action.payload.map((currency: ApiCurrency) => ({ ...currency, icon: currency.label }))
      })
      .addCase(getCurrencies.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })

      // GET VACANCY SOURCES
      .addCase(getSources.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(getSources.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.sources = action.payload.map((s: { id: internal; title: string }) => ({ id: s.id, name: s.title }))
      })
      .addCase(getSources.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })

      // CREATE RESPONSE
      .addCase(createResponse.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(createResponse.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.responses = [...state.responses, action.payload]
      })
      .addCase(createResponse.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })

      // PATCH RESPONSE
      .addCase(patchResponse.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(patchResponse.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.responses = state.responses.map((response) =>
          response.id === action.payload.id ? action.payload : response,
        )
      })
      .addCase(patchResponse.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })

      // MOVE CARDS
      .addCase(moveCards.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(moveCards.fulfilled, (state, action) => {
        state.status = 'succeeded'

        // state.responses = action.payload
      })
      .addCase(moveCards.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })

      // DELETE RESPONSE
      .addCase(deleteResponse.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(deleteResponse.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.responses = state.responses.filter((response) => response.id !== action.payload)
      })
      .addCase(deleteResponse.rejected, (state, action) => {
        state.isError = true
        state.errorInfo = action.payload as IAPIError
        state.status = 'failed'
      })
  },
})

export const {
  updateResponseStatus,
  cancelDragging,
  finishDragging,
  setResponses,
  addToQueue,
  removeFromQueue,
  setSearchQuery,
} = diarySlice.actions

export default diarySlice
