import { configureStore, ThunkAction, Action, combineReducers } from '@reduxjs/toolkit'
import thunk from 'redux-thunk'
import { persistReducer } from 'redux-persist'
import storage from 'redux-persist/lib/storage'
import { FLUSH, PAUSE, PERSIST, PURGE, REGISTER, REHYDRATE } from 'redux-persist/es/constants'
import authSlice, { initialState } from '../slices/auth-slice'
import onboardingSlice from '../slices/onboarding-slice'
import { authMiddleware } from './middleware'
import vacanciesPageSlice from '../slices/vacancies-page-slice'
import metricsSlice from '../slices/metrics-slice'
import appSlice from '../slices/app-slice'
import achievementsSlice from '../slices/achievements-slice'
import diarySlice from '../slices/diary-slice'
import websocketSlice from '../slices/websocket-slice'
import { socketMiddleware } from './socketMiddleware'
import studentVacanciesSlice from '../slices/student-vacancies-slice'
import statisticsMetricsSlice from '../slices/statistics-metrics-slice'
import hrNotificationsSlice from '../slices/hr-notifications-slice'
import companiesPageSlice from '../slices/companies-page-slice'
import partnersFormSlice from '../slices/partners-form-slice'
import projectCreationSlice from '../slices/project-creation-slice'
import workshopProjectsListPageSlice from '../slices/workshop-projects-list-page-slice'
import studentProjectsSlice from '../slices/student-workshop-slice'

//        
//    ,       
const persistConfig = {
  key: 'root',
  storage,
  blacklist: [
    'companiesPage',
    'app',
    'auth',
    'onboarding',
    'vacanciesPage',
    'metrics',
    'achievements',
    'diary',
    'ws',
    'statisticsMetrics',
    'hrNotifications',
    'partnersForm',
    'projectCreationPage',
  ],
}
//    
const persistCompanyPageConfig = {
  key: 'companiesPage',
  storage,
}

//      
const persistStudentVacanciesPageConfig = {
  key: 'studentVacancies',
  storage,
}

//      
const persistProjectManagerPageConfig = {
  key: 'workshopProjectsListPage',
  storage,
}

//      
const persistStudentsProjectsPageConfig = {
  key: 'studentsWorkshop',
  storage,
}

// TODO  ,       
// const projectCreationPageConfig = {
//   key: 'projectCreationPage',
//   storage,
// }

export const rootReducer = combineReducers({
  app: appSlice.reducer,
  auth: authSlice.reducer,
  onboarding: onboardingSlice.reducer,
  vacanciesPage: vacanciesPageSlice.reducer,
  //      
  companiesPage: persistReducer(persistCompanyPageConfig, companiesPageSlice.reducer),
  metrics: metricsSlice.reducer,
  achievements: achievementsSlice.reducer,
  diary: diarySlice.reducer,
  ws: websocketSlice.reducer,
  //      
  studentVacancies: persistReducer(persistStudentVacanciesPageConfig, studentVacanciesSlice.reducer),
  statisticsMetrics: statisticsMetricsSlice.reducer,
  hrNotifications: hrNotificationsSlice.reducer,
  partnersForm: partnersFormSlice.reducer,
  //      
  // projectCreationPage: persistReducer(projectCreationPageConfig, projectCreationSlice.reducer),
  projectCreationPage: projectCreationSlice.reducer,
  //    
  workshopProjectsListPage: persistReducer(persistProjectManagerPageConfig, workshopProjectsListPageSlice.reducer),
  studentsWorkshop: persistReducer(persistStudentsProjectsPageConfig, studentProjectsSlice.reducer),
})

//   
const persistedReducer = persistReducer(persistConfig, rootReducer)

const loadFromLocalStorage = () => {
  try {
    const serialisedState = localStorage.getItem('auth')
    if (serialisedState === null) return undefined
    return JSON.parse(serialisedState)
  } catch (e) {
    return undefined
  }
}

const loadedAuthState = loadFromLocalStorage()

/*  thunk   
 *  When using Redux Persist without using the Thunk middleware, wed get an error in the browsers console
 *  reading a non-serializable value was detected in the state.
 * */

export const setupStore = () =>
  configureStore({
    reducer: persistedReducer,
    middleware: (getDefaultMiddleware) =>
      getDefaultMiddleware({
        serializableCheck: {
          ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
        },
      }).concat(thunk, authMiddleware, socketMiddleware()),
    preloadedState: { auth: { ...initialState, ...loadedAuthState } },
  })

export type AppStore = ReturnType<typeof setupStore>
export type AppDispatch = AppStore['dispatch']
export type TRootState = ReturnType<typeof rootReducer>
export type AppThunk<ReturnType = void> = ThunkAction<ReturnType, TRootState, unknown, Action<string>>
