import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { v4 as uuidv4 } from 'uuid'
import { IAPIError } from '../types/global-types'
import {
  IFilterJson,
  IInitialState,
  IInfoPopupAction,
} from '../types/workshop-projects-list-page-types/workshop-projects-list-page-types'
import {
  deleteProject,
  getDirectories,
  getProjectsList,
  getProjectStudents,
  getProjectTeam,
  patchProjectInfo,
  patchProjectStatus,
  patchStudentStatus,
  patchTeamStatus,
} from '../axios/workshop-projects-list-page'

export const initialState: IInitialState = {
  filtersJSON: {} as IFilterJson,
  projectToEdit: null,
  projects: [],
  projectToClose: null,
  projectToDraft: null,
  isLoading: false,
  isNetworkError: false,
  networkError: null,
  rowsCount: 0,
  updateStatus: '',
  isLockUpdate: false,
  // TODO    
  // filters: []
  infoPopupTitle: '',
  infoPopupSubtitle: '',
  infoPopupButtonTitle: '',
  isInfoPopupOpen: false,
  infoPopupType: '',
  deleteStatus: '',
  projectStudents: [],
  projectTeam: [],
  projectStudentsRowsCount: 0,
  projectTeamRowsCount: 0,
}

const workshopProjectsListPageSlice = createSlice({
  name: 'workshopProjectsListPage',
  initialState,
  reducers: {
    // TODO    -  
    createProject(state, action) {
      state.projects.unshift(action.payload)
    },
    openInfoPopup(state, action: PayloadAction<IInfoPopupAction>) {
      state.infoPopupTitle = action.payload.title
      state.infoPopupSubtitle = action.payload.subtitle
      state.infoPopupButtonTitle = action.payload.buttonTitle
      state.infoPopupType = action.payload.type
      state.isInfoPopupOpen = true
    },
    closeInfoPopup(state) {
      state.isInfoPopupOpen = false
      state.infoPopupTitle = ''
      state.infoPopupSubtitle = ''
      state.infoPopupType = ''
    },
    setIsLockUpdate(state, action) {
      state.isLockUpdate = action.payload
    },
    setProjectToEdit(state, action) {
      state.projectToEdit = action.payload
    },
    resetUpdateStatus(state) {
      state.updateStatus = ''
    },
    resetDeleteStatus(state) {
      state.deleteStatus = ''
    },
    setProjectToClose(state, action) {
      state.projectToClose = action.payload
    },

    setProjectToDraft(state, action) {
      state.projectToDraft = action.payload
    },

    setFiltersJSON(state, action) {
      state.filtersJSON = action.payload
    },
  },
  extraReducers: (builder) => {
    builder
      // get projects
      .addCase(getProjectsList.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getProjectsList.fulfilled, (state, action) => {
        state.isLoading = false
        // state.projects = action.payload.results
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        state.projects = action.payload
        state.rowsCount = action.payload.length

        // if (action.payload.previous === null) {
        //   state.vacancies = action.payload.results
        // } else {
        //   state.vacancies = state.vacancies.concat(...action.payload.results)
        // }
        // state.rowsCount = action.payload.count
      })
      .addCase(getProjectsList.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get Directories
      .addCase(getDirectories.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getDirectories.fulfilled, (state, action) => {
        state.isLoading = false
        state.filtersJSON = action.payload
      })
      .addCase(getDirectories.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // patch project status
      // get Directories
      .addCase(patchProjectStatus.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchProjectStatus.fulfilled, (state, action) => {
        state.isLoading = false
        const { projectId, status } = action.payload
        const projects_statuses = [
          {
            id: 1,
            status: '',
            label: '',
            value: '',
            color: 'hsla(219, 100%, 97%, 1)',
          },
          {
            id: 2,
            status: ' ',
            label: ' ',
            value: ' ',
            color: 'hsla(254, 54%, 85%, 1)',
          },
          {
            id: 3,
            status: ' ',
            label: ' ',
            value: ' ',
            color: 'hsla(51, 100%, 91%, 1)',
          },
          {
            id: 4,
            status: '',
            label: '',
            value: '',
            color: 'hsla(217, 100%, 84%, 1)',
          },
          {
            id: 5,
            status: ' ',
            label: ' ',
            value: ' ',
            color: 'hsla(141, 40%, 83%, 1)',
          },
          {
            id: 6,
            status: ' ',
            label: ' ',
            value: ' ',
            color: 'hsla(33, 23%, 56%, 0.26)',
          },
          {
            id: 7,
            status: ' ',
            label: ' ',
            value: ' ',
            color: 'hsla(33, 100%, 87%, 1)',
          },
          {
            id: 8,
            status: ' ',
            label: ' ',
            value: ' ',
            color: 'hsla(214, 11%, 88%, 1)',
          },
          {
            id: 9,
            status: '',
            label: '',
            value: '',
            color: 'hsla(214, 11%, 88%, 1)',
          },
          {
            id: 10,
            status: '',
            label: '',
            value: '',
            color: 'hsla(346, 100%, 93%, 1)',
          },
        ]
        const newProjectsState = [...state.projects]
        const editedIndex = newProjectsState.findIndex((p) => p.id === projectId)
        newProjectsState[editedIndex] = {
          ...newProjectsState[editedIndex],
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          status: projects_statuses.find((item) => item.status === status),
        }
        state.projects = [...newProjectsState]
        state.updateStatus = 'ok'
      })
      .addCase(patchProjectStatus.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      .addCase(patchProjectInfo.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchProjectInfo.fulfilled, (state, action) => {
        state.isLoading = false
        const { projectId, fieldName, fieldValue } = action.payload
        const newProjectsState = [...state.projects]
        const editedProject = newProjectsState.find((p) => p.id === projectId)
        if (editedProject) {
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          editedProject[fieldName] = fieldValue
        }
        state.projects = [...newProjectsState]

        // newProjectsState[editedIndex] = {
        //   ...newProjectsState[editedIndex],
        //   // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        //   // @ts-ignore
        //   status: projects_statuses.find((item) => item.status === status),
        // }
        state.projects = [...newProjectsState]
        state.updateStatus = 'ok'
      })
      .addCase(patchProjectInfo.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
      // Delete project
      .addCase(deleteProject.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(deleteProject.fulfilled, (state, action) => {
        state.isLoading = false
        const id = action.payload
        const newProjectsState = [...state.projects]
        const editedProjectIndex = newProjectsState.findIndex((p) => p.id === id)
        newProjectsState.splice(editedProjectIndex, 1)
        state.projects = [...newProjectsState]
        state.deleteStatus = 'ok'
      })
      .addCase(deleteProject.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get projects students
      .addCase(getProjectStudents.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getProjectStudents.fulfilled, (state, action) => {
        state.isLoading = false
        // state.projects = action.payload.results
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        state.projectStudents = action.payload
        state.projectStudentsRowsCount = action.payload.length

        // if (action.payload.previous === null) {
        //   state.vacancies = action.payload.results
        // } else {
        //   state.vacancies = state.vacancies.concat(...action.payload.results)
        // }
        // state.rowsCount = action.payload.count
      })
      .addCase(getProjectStudents.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get projects team
      .addCase(getProjectTeam.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getProjectTeam.fulfilled, (state, action) => {
        state.isLoading = false
        // state.projects = action.payload.results
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        state.projectTeam = action.payload
        state.projectTeamRowsCount = action.payload.length

        // if (action.payload.previous === null) {
        //   state.vacancies = action.payload.results
        // } else {
        //   state.vacancies = state.vacancies.concat(...action.payload.results)
        // }
        // state.rowsCount = action.payload.count
      })
      .addCase(getProjectTeam.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
      // Patch students status
      .addCase(patchStudentStatus.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchStudentStatus.fulfilled, (state, action) => {
        state.isLoading = false
        const { id, fieldName, fieldValue } = action.payload
        const newProjectsStudents = [...state.projectStudents]
        const editedProject = newProjectsStudents.find((p) => p.id === id)
        if (editedProject) {
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          editedProject[fieldName] = fieldValue
        }

        // newProjectsState[editedIndex] = {
        //   ...newProjectsState[editedIndex],
        //   // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        //   // @ts-ignore
        //   status: projects_statuses.find((item) => item.status === status),
        // }
        state.updateStatus = 'ok'
      })
      .addCase(patchStudentStatus.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // Patch team status
      .addCase(patchTeamStatus.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchTeamStatus.fulfilled, (state, action) => {
        state.isLoading = false
        const { id, fieldName, fieldValue } = action.payload
        const newProjectsTeam = [...state.projectTeam]
        const editedProject = newProjectsTeam.find((p) => p.id === id)
        if (editedProject) {
          // TODO ,  
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          editedProject[fieldName] = [fieldValue]
        }

        // newProjectsState[editedIndex] = {
        //   ...newProjectsState[editedIndex],
        //   // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        //   // @ts-ignore
        //   status: projects_statuses.find((item) => item.status === status),
        // }
        state.updateStatus = 'ok'
      })
      .addCase(patchTeamStatus.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
  },
})

export const {
  setFiltersJSON,
  setProjectToClose,
  setProjectToDraft,
  resetUpdateStatus,
  setProjectToEdit,
  setIsLockUpdate,
  openInfoPopup,
  closeInfoPopup,
  resetDeleteStatus,
  createProject,
} = workshopProjectsListPageSlice.actions

export default workshopProjectsListPageSlice
