import { createSlice } from '@reduxjs/toolkit'
import { v4 as uuidv4 } from 'uuid'
import {
  IDirectories,
  IFilterJson,
  IHRVacancyHeader,
  IInitialState,
  IStudentRawData,
  TStudentRawArrayData,
  TStudentRowArray,
} from '../types/vacacies-page-types/vacancies-page-types'
import {
  deleteVacancy,
  getDirectories,
  getInfo,
  getStudentProfile,
  getVacanciesList,
  getVacanciesStatistic,
  patchStudentStatus,
  patchVacancyInfo,
  patchVacancyStatus,
  postDraftVacancy,
} from '../axios/vacancies-page'
import { IAPIError } from '../types/global-types'

const getRows = (rawData: TStudentRawArrayData): TStudentRowArray => {
  const result = []
  const offeredStudents = rawData.filter((s) => s.job_found)
  const otherStudents = rawData.filter((s) => !s.job_found)
  const isOfferedStudents = offeredStudents.length > 0
  const isOtherStudents = otherStudents.length > 0
  if (isOfferedStudents)
    result.push({ id: 'OFFERED', title: `      (${offeredStudents.length})` })
  rawData.map((s) => (s.job_found ? result.push(s) : null))
  if (isOtherStudents) result.push({ id: 'OTHER', title: `  (${otherStudents.length})` })
  rawData.map((s) => (!s.job_found ? result.push(s) : null))
  return result
}

export const initialState: IInitialState = {
  filtersJSON: {} as IFilterJson,
  directoriesData: {} as IDirectories,
  isOpenVacancyPopup: false,
  vacancyToArchive: null,
  vacancyToDelete: null,
  isInfoPopupOpen: false,
  vacancyToEdit: null,
  vacancies: [],
  statistic: [],
  isLoading: false,
  isOpenFoundJobPopup: false,
  studentToEdit: null,
  isNetworkError: false,
  networkError: null,
  patchNetworkError: null,
  infoData: [],
  rows: [],
  updateStatus: '',
  updateStudentStatus: false,
  rowsCount: 0,
  header: {} as IHRVacancyHeader,
  isDraftPopupOpen: false,
  draftFormData: null,
  studentProfile: {},
  isOpenVacancyAcceleratePopup: false,
  isSuccessDeleteVacancy: false,
}

const vacanciesPageSlice = createSlice({
  name: 'vacancies-page',
  initialState,
  reducers: {
    setIsSuccessDeleteVacancy(state, action) {
      state.isSuccessDeleteVacancy = action.payload
    },
    setIsOpenVacancyAcceleratePopup(state, action) {
      state.isOpenVacancyAcceleratePopup = action.payload
    },
    setUpdatedStudentStatus(state, action) {
      const newInfoRows = [...state.infoData]
      newInfoRows[newInfoRows?.findIndex((el) => el.id === state.studentToEdit?.id)].status.value = action.payload
      state.rows = getRows(newInfoRows)
      state.updateStatus = 'ok'
      state.updateStudentStatus = true
    },
    setFiltersJSON(state, action) {
      state.filtersJSON = action.payload
    },
    setDirectoriesData(state, action) {
      state.directoriesData = action.payload
    },
    setIsOpenVacancyPopup(state, action) {
      state.isOpenVacancyPopup = action.payload
    },
    setVacancyToArchive(state, action) {
      state.vacancyToArchive = action.payload
    },
    setVacancyToDelete(state, action) {
      state.vacancyToDelete = action.payload
    },
    setIsInfoPopupOpen(state, action) {
      state.isInfoPopupOpen = action.payload
    },
    setVacancyToEdit(state, action) {
      state.vacancyToEdit = action.payload
    },
    setVacancies(state, action) {
      state.vacancies = action.payload
    },
    setIsLoading(state, action) {
      state.isLoading = action.payload
    },
    setIsOpenFoundJobPopup(state, action) {
      state.isOpenFoundJobPopup = action.payload
    },
    setStudentToEdit(state, action) {
      state.studentToEdit = action.payload
    },
    resetUpdateStatus(state) {
      state.updateStatus = ''
    },
    resetUpdateStudentStatus(state) {
      state.updateStudentStatus = false
    },
    setDraftPopupState(state, action) {
      state.isDraftPopupOpen = action.payload
    },
    setDraftFormData(state, action) {
      state.draftFormData = action.payload
    },
    clearPatchError(state) {
      state.networkError = null
      state.patchNetworkError = null
      state.isNetworkError = false
    },
    setHeader(state, action) {
      state.header = action.payload
    },
  },
  extraReducers: (builder) => {
    builder
      // get Vacancies
      .addCase(getVacanciesList.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getVacanciesList.fulfilled, (state, action) => {
        state.isLoading = false
        state.vacancies = action.payload.results
        // if (action.payload.previous === null) {
        //   state.vacancies = action.payload.results
        // } else {
        //   state.vacancies = state.vacancies.concat(...action.payload.results)
        // }
        state.rowsCount = action.payload.count
      })
      .addCase(getVacanciesList.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get statistic
      .addCase(getVacanciesStatistic.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getVacanciesStatistic.fulfilled, (state, action) => {
        state.isLoading = false
        const stats = action.payload
        state.statistic = [
          {
            title: ' ',
            value: stats.vacancy_count,
            dynamicValue: null,
            id: uuidv4(),
          },
          {
            title: '% ',
            value: stats.referral,
            dynamicValue: stats.dynamic_referral,
            id: uuidv4(),
          },
          {
            title: '% ',
            subtitle: '',
            value: stats.repeated,
            dynamicValue: stats.dynamic_repeated,
            id: uuidv4(),
          },
          {
            title: '% ',
            value: stats.sourcing,
            dynamicValue: stats.dynamic_sourcing,
            id: uuidv4(),
          },
          {
            title: '',
            value: stats.employed,
            dynamicValue: null,
            id: uuidv4(),
          },
          {
            title: '% ',
            subtitle: '',
            value: stats.vacancies_closed,
            dynamicValue: stats.dynamic_vacancies_closed,
            id: uuidv4(),
          },
        ]
      })
      .addCase(getVacanciesStatistic.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get Directories
      .addCase(getDirectories.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getDirectories.fulfilled, (state, action) => {
        state.isLoading = false
        state.filtersJSON = action.payload.filters
        state.directoriesData = action.payload.directoriesData
      })
      .addCase(getDirectories.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // patch Vacancies
      .addCase(patchVacancyStatus.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchVacancyStatus.fulfilled, (state, action) => {
        state.isLoading = false
        const { updatedRow } = action.payload
        const newVacancies = [...state.vacancies]
        newVacancies[newVacancies.findIndex((el) => el.id === updatedRow.id)] = updatedRow
        state.vacancies = newVacancies
        state.updateStatus = 'ok'
      })
      .addCase(patchVacancyStatus.rejected, (state, action) => {
        state.isLoading = false
        state.patchNetworkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // delete Vacancies
      .addCase(deleteVacancy.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(deleteVacancy.fulfilled, (state, action) => {
        state.isLoading = false
        state.isSuccessDeleteVacancy = true
      })
      .addCase(deleteVacancy.rejected, (state, action) => {
        state.isLoading = false
        state.patchNetworkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // patch student's status
      .addCase(patchStudentStatus.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchStudentStatus.fulfilled, (state, action) => {
        state.isLoading = false
        const { updatedRow } = action.payload
        const newInfoRows: Array<IStudentRawData> = [...state.infoData]
        newInfoRows[newInfoRows.findIndex((el) => el.id === updatedRow.id)].status.value = updatedRow.status
        state.rows = getRows(newInfoRows)
        state.updateStatus = 'ok'
        state.updateStudentStatus = true
      })
      .addCase(patchStudentStatus.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get Info about vacancy
      .addCase(getInfo.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getInfo.fulfilled, (state, action) => {
        state.isLoading = false
        state.infoData = action.payload.tableData
        state.header = action.payload.headerData
        state.rows = getRows(action.payload.tableData)
      })
      .addCase(getInfo.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // get student profile
      .addCase(getStudentProfile.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(getStudentProfile.fulfilled, (state, action) => {
        state.isLoading = false
        state.studentProfile = action.payload
      })
      .addCase(getStudentProfile.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // patch vacancy info
      .addCase(patchVacancyInfo.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(patchVacancyInfo.fulfilled, (state, action) => {
        state.isLoading = false
        state.header = action.payload
        state.vacancies = state.vacancies.map((v) => (v.id !== action.payload?.id ? v : action.payload))
        // const updatedRow = action.payload
        // const newVacancies = [...state.vacancies]
        //      .      +   
        // newVacancies[newVacancies.findIndex((el) => el.id === updatedRow.id)] = {
        //   ...newVacancies[newVacancies.findIndex((el) => el.id === updatedRow.id)],
        //   ...updatedRow,
        //   source: state.filtersJSON.sources.find((item) => item.id === updatedRow.source)?.name,
        // }
        // state.vacancies = newVacancies
      })
      .addCase(patchVacancyInfo.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })

      // post Draft Vacancy
      .addCase(postDraftVacancy.pending, (state) => {
        state.isLoading = true
        state.isNetworkError = false
        state.networkError = null
      })
      .addCase(postDraftVacancy.fulfilled, (state, action) => {
        state.isLoading = false
      })
      .addCase(postDraftVacancy.rejected, (state, action) => {
        state.isLoading = false
        state.networkError = action.payload as IAPIError
        state.isNetworkError = true
      })
  },
})

export const {
  setFiltersJSON,
  setDirectoriesData,
  setIsOpenVacancyPopup,
  setVacancyToArchive,
  setIsInfoPopupOpen,
  setVacancyToEdit,
  setVacancies,
  setIsLoading,
  setIsOpenFoundJobPopup,
  setStudentToEdit,
  resetUpdateStatus,
  resetUpdateStudentStatus,
  setDraftPopupState,
  setDraftFormData,
  clearPatchError,
  setUpdatedStudentStatus,
  setIsOpenVacancyAcceleratePopup,
  setHeader,
  setVacancyToDelete,
  setIsSuccessDeleteVacancy,
} = vacanciesPageSlice.actions

export default vacanciesPageSlice
