import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { ExperienceInfo, OnboardingBaseData, PersonalInfo, ResumeInfo } from '../types/onboarding/onboarding-types'
import { getOnboardingBaseData } from '../axios/onboarding'
import { IAPIError, IRequestInfo } from '../types/global-types'

type TInitialState = IRequestInfo & {
  baseData: OnboardingBaseData | null
}

const initialState: TInitialState = {
  status: 'idle',
  isError: false,
  errorInfo: null,
  baseData: null,
}

export const onboardingSlice = createSlice({
  name: 'onboarding',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(getOnboardingBaseData.pending, (state) => {
        state.status = 'pending'
        state.isError = false
        state.errorInfo = null
      })
      .addCase(getOnboardingBaseData.fulfilled, (state, action) => {
        state.status = 'succeeded'
        state.baseData = action.payload
      })
      .addCase(getOnboardingBaseData.rejected, (state, action) => {
        state.status = 'failed'
        state.isError = true
        state.errorInfo = action.payload as IAPIError
      })
  },
})

export default onboardingSlice
