import { createAsyncThunk } from '@reduxjs/toolkit'
import { AxiosError } from 'axios'
import {
  API_HR_PART_DIRECTORY_ENDPOINT,
  API_COMPANIES_LIST,
  API_COMPANIES_STATUSES,
  API_COMPANY_EMPLOYED_STUDENT,
  API_COMPANY_VACANCIES,
  API_COMPANY_ID,
  API_GET_FILTERS_STUDENTS,
  API_STUDENT_BY_ID_ENDPOINT,
  API_HR_PART_VACANCY_BY_ID_ENDPOINT,
  API_HR_PART_COMPANY_SIZE_ENDPOINT,
  API_HR_PART_COMPANY_SCOPE_ENDPOINT,
  API_HR_PART_COMPANY_TYPE_ENDPOINT,
  API_COMPANY_DRAFT_ENDPOINT,
} from '../../utils/constants'
import api from './settings'
import {
  ICompanyStatus,
  IDirectoriesResponse,
  TGetCompaniesData,
  TGetCompanyVacancies,
  TPatchData,
  TPatchInfoData,
  TPatchStudentData,
  TPatchVacancyData,
} from '../types/companies-page-types/companies-page-types'

export const getCompaniesList = createAsyncThunk(
  'companies-page/getCompaniesList',
  async (data: TGetCompaniesData, { rejectWithValue }) => {
    try {
      const { paramsSerializer } = data
      const response = await api.get(API_COMPANIES_LIST, {
        params: { ...data.hook.filters },
        paramsSerializer,
      })
      // return Promise.resolve(response.data)
      return response?.data
    } catch (e) {
      const err = e as AxiosError
      return rejectWithValue(err?.response)
    }
  },
)

