export const getDirectories = createAsyncThunk(
  'companies-page/getDirectories',
  async (_: void, { rejectWithValue }) => {
    try {
      const response = await api.get(API_HR_PART_DIRECTORY_ENDPOINT)
      const rawData: IDirectoriesResponse = response?.data
      const courses = rawData?.courses
      const statuses = rawData?.vacancies_status?.map((s) => ({ ...s, label: s?.value }))
      const studentStatuses = rawData?.student_status?.map((s) => ({ ...s, label: s?.value }))
      const currencies = rawData?.currencies?.map((currency) => ({
        id: currency.id,
        name: currency.name,
        icon: currency.label,
      }))
      const employmentItems = rawData?.employment?.map((e) => ({ ...e, name: e?.value }))
      const workFormatItems = rawData?.work_format?.map((w) => ({ ...w, name: w?.value }))
      const jobApplicationItems = rawData?.design_formats?.map((d) => ({ ...d, name: d?.value }))
      const experienceItems = rawData?.experience_types
      const educationItems = rawData?.education_types
      const vacanciesStatus = rawData?.vacancies_status?.map((s) => ({ ...s, label: s?.value }))
      const sizes_response = await api.get(API_HR_PART_COMPANY_SIZE_ENDPOINT)
      const types_response = await api.get(API_HR_PART_COMPANY_TYPE_ENDPOINT)
      const scopes_response = await api.get(API_HR_PART_COMPANY_SCOPE_ENDPOINT)

      return {
        filters: {
          courses,
          // statuses,
        },
        directoriesData: {
          courses,
          statuses,
          studentStatuses,
          currencies,
          employmentItems,
          workFormatItems,
          jobApplicationItems,
          experienceItems,
          educationItems,
          vacanciesStatus,
          sizes: sizes_response?.data?.map((s: { id: number; value: string }) => ({ ...s, name: s?.value })),
          types: types_response?.data?.map((t: { id: number; value: string }) => ({ ...t, name: t?.value })),
          scopes: scopes_response?.data?.map((s: { id: number; value: string }) => ({ ...s, name: s?.value })),
        },
      }
    } catch (e) {
      const typedError = e as AxiosError
      return rejectWithValue(typedError?.response)
    }
  },
)

