export const patchCompanyStatus = createAsyncThunk(
  'companies-page/patchCompanyStatus',
  async (data: TPatchData, { rejectWithValue }) => {
    try {
      const response = await api.patch(API_COMPANY_ID(data.companyId), {
        status: data.status.status,
      })
      return {
        updatedRow: response.data,
      }
    } catch (e) {
      const typedError = e as AxiosError
      return rejectWithValue(typedError?.response)
    }
  },
)

