export const patchCompanyInfo = createAsyncThunk(
  'companies-page/patchCompanyInfo',
  async (data: TPatchInfoData, { rejectWithValue }) => {
    try {
      const { companyId } = data
      const patchData = { ...data }
      delete patchData.companyId
      const response = await api.patch(API_COMPANY_ID(companyId), {
        ...patchData,
      })
      return response.data
    } catch (e) {
      const typedError = e as AxiosError
      return rejectWithValue(typedError?.response)
    }
  },
)

