import { createAsyncThunk } from '@reduxjs/toolkit'
import { AxiosError } from 'axios'
import {
  API_HR_PART_VACANCY_ENDPOINT,
  API_HR_PART_DIRECTORY_ENDPOINT,
  API_HR_PART_VACANCY_BY_ID_ENDPOINT,
  API_STUDENT_BY_ID_ENDPOINT,
  API_HR_PART_VACANCY_INFO_ENDPOINT,
  API_HR_PART_STATISTIC_ENDPOINT,
  API_HR_PART_HR_SOURCES_ENDPOINT,
  API_HR_PART_HR_PROFILES_ENDPOINT,
  API_HR_PART_DRAFT_VACANCY_ENDPOINT,
} from '../../utils/constants'
import api from './settings'
import {
  IDirectoriesResponse,
  IHRVacancyHeader,
  IParams,
  IProfilesDirectoryResponse,
  ISources,
  ISourcesDirectoryResponse,
  TGetVacanciesData,
  TGetVacancyInfo,
  TPatchData,
  TPatchStudentData,
} from '../types/vacacies-page-types/vacancies-page-types'

export const getVacanciesList = createAsyncThunk(
  'vacancies-page/getVacanciesList',
  async (data: TGetVacanciesData, { rejectWithValue }) => {
    try {
      const { paramsSerializer } = data
      const response = await api.get(API_HR_PART_VACANCY_ENDPOINT, {
        params: { ...data.hook.filters },
        paramsSerializer,
      })
      // return Promise.resolve(response.data)
      return response?.data
    } catch (e) {
      const err = e as AxiosError
      return rejectWithValue(err?.response)
    }
  },
)

