export const getDirectories = createAsyncThunk(
  'vacancies-page/getDirectories',
  async (_: void, { rejectWithValue }) => {
    try {
      const response = await api.get(API_HR_PART_DIRECTORY_ENDPOINT)
      const response_sources = await api.get(API_HR_PART_HR_SOURCES_ENDPOINT)
      const response_profiles = await api.get(API_HR_PART_HR_PROFILES_ENDPOINT)
      const sourcesData: Array<ISourcesDirectoryResponse> = response_sources?.data
      const profilesData: Array<IProfilesDirectoryResponse> = response_profiles?.data
      const rawData: IDirectoriesResponse = response?.data
      const courses = rawData?.courses
      const statuses = rawData?.vacancies_status?.map((s) => ({ ...s, label: s?.value }))
      const studentStatuses = rawData?.student_status?.map((s) => ({ ...s, label: s?.value }))
      const currencies = rawData?.currencies?.map((currency) => ({
        id: currency.id,
        name: currency.name,
        icon: currency.label,
      }))
      const employmentItems = rawData?.employment?.map((e) => ({ ...e, name: e?.value }))
      const workFormatItems = rawData?.work_format?.map((w) => ({ ...w, name: w?.value }))
      const jobApplicationItems = rawData?.design_formats?.map((d) => ({ ...d, name: d?.value }))
      const experienceItems = rawData?.experience_types
      const educationItems = rawData?.education_types
      const profilesItems = profilesData?.map((p) => ({ id: p.id, name: `${p.first_name} ${p.last_name}` }))
      const sourcesItems: Array<ISources> = sourcesData?.map((s) => ({ id: s.id, name: s.title }))
      return {
        filters: {
          courses,
          statuses,
          profiles: profilesItems,
          sources: sourcesItems,
        },
        directoriesData: {
          courses,
          statuses,
          studentStatuses,
          currencies,
          employmentItems,
          workFormatItems,
          jobApplicationItems,
          experienceItems,
          educationItems,
          profilesItems,
          sourcesItems,
        },
      }
    } catch (e) {
      const typedError = e as AxiosError
      return rejectWithValue(typedError?.response)
    }
  },
)

export const patchVacancyStatus = createAsyncThunk(
  'vacancies-page/patchVacancyStatus',
  async (data: TPatchData, { rejectWithValue }) => {
    try {
      const response = await api.patch(API_HR_PART_VACANCY_BY_ID_ENDPOINT(data.vacancyId), {
        vacancy_status: data.status.status,
      })
      return {
        updatedRow: response.data,
      }
    } catch (e) {
      const typedError = e as AxiosError
      return rejectWithValue(typedError?.response)
    }
  },
)

