export const getInfo = createAsyncThunk(
  'vacancies-page/getInfo',
  async (data: TGetVacancyInfo, { rejectWithValue }) => {
    const params: IParams = {}
    if (data.searchQuery) params.search = data.searchQuery
    try {
      const response = await api.get(API_HR_PART_VACANCY_INFO_ENDPOINT(data.id), {
        params,
      })
      const header_response = await api.get(API_HR_PART_VACANCY_BY_ID_ENDPOINT(data.id))

      return {
        headerData: header_response.data,
        tableData: response.data,
      }
    } catch (e) {
      const typedError = e as AxiosError
      return rejectWithValue(typedError?.response)
    }
  },
)

